import React, { useState } from "react";
import { Skeleton } from 'antd';
import StatisticWidget from 'components/shared-components/StatisticWidget';
import { withRouter } from 'react-router-dom';
import { gql, useQuery } from '@apollo/client';
import IntlMessage from "components/util-components/IntlMessage";
import { ParentSVG, StudentSVG, SubjectSVG, TeacherSVG } from "assets/svg/menu-icon";
import News from "../news/news";

const DASHBOARD = gql`
    query dashboard {
        dashboard {
            studentCount
            teacherCount
            parentCount
            subjectCount
            logins {
                loginDate
                loginCount
            }
        }
    }
`

export const DefaultDashboard = () => {

    const [countData, setCountData] = useState([])

    const { loading } = useQuery(DASHBOARD, {
        onCompleted: data => {
            setCountData([
                {
                    title: <IntlMessage id="student-registration" />,
                    value: data.dashboard.studentCount,
                    svg: StudentSVG,
                    color: 'rgba(159, 177, 183, .1)'
                },
                {
                    title: <IntlMessage id="teacher-registration" />,
                    value: data.dashboard.teacherCount,
                    svg: TeacherSVG,
                    color: 'rgba(159, 177, 183, .1)'
                },
                {
                    title: <IntlMessage id="parent-registration" />,
                    value: data.dashboard.parentCount,
                    svg: ParentSVG,
                    color: 'rgba(159, 177, 183, .1)'
                },
                {
                    title: <IntlMessage id="subject-registration" />,
                    value: data.dashboard.subjectCount,
                    svg: SubjectSVG,
                    color: 'rgba(159, 177, 183, .1)'
                }
            ])
        }
    });

    return (
        <div className="space-y-6">
            <Skeleton loading={loading} active rows={6}>
                <div className="grid gap-4 md:grid-cols-4">
                    {countData?.map((elm, i) => (
                        <StatisticWidget
                            key={i}
                            color={elm.color}
                            title={elm.title}
                            value={elm.value}
                            Svg={elm.svg}
                        />
                    ))}
                </div>
            </Skeleton>
            <News />
        </div>
    )
}

export default withRouter(DefaultDashboard);
