import { useQuery } from '@apollo/client'
import Loading from 'components/shared-components/Loading'
import { BASE_SERVER_URL } from 'configs/AppConfig'
import { ALL_NEWS } from 'graphql/news'
import moment from 'moment'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

export default function News() {

    const [offset, setOffset] = useState(0)
    const [canFetch, setCanFetch] = useState(true)
    const [news, setNews] = useState([])

    const { loading } = useQuery(ALL_NEWS, {
        variables: { offset: offset },
        onCompleted: res => {
            if (res.allNews.length < 12) {
                setCanFetch(false)
            }
            setNews(prevState => [...prevState, ...res.allNews])
        }
    })

    useEffect(() => {
        const handleScroll = () => {
            const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            const scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
            const clientHeight = document.documentElement.clientHeight || window.innerHeight;

            if (scrollTop + clientHeight >= scrollHeight - 100) {
                setOffset(prevState => prevState + 12)
            }
        };

        if (canFetch && !loading) {
            window.addEventListener('scroll', handleScroll);

        } else {
            window.removeEventListener('scroll', handleScroll);
        }

        // Cleanup the event listener when the component unmounts
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, [canFetch, loading]);

    return (
        <>
            <div className='grid grid-cols-1 gap-4 md:grid-cols-4'>
                {
                    news.map(item => (
                        <Link to={`/app/news/${item.id}`} key={item.id} className="bg-white overflow-hidden border-b-4 border-emind ">
                            <img src={BASE_SERVER_URL + item.image} alt={item.image} className="w-full object-cover h-32 sm:h-48 md:h-64" />
                            <div className="p-4 md:p-6">
                                <h3 className="font-semibold mb-2 text-xl leading-tight sm:leading-normal">{item.title}</h3>
                                <div className="text-sm flex items-center">
                                    <svg className="opacity-75 mr-2" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_1" x="0px" y="0px" width={12} height={12} viewBox="0 0 97.16 97.16" style={{ enableBackground: 'new 0 0 97.16 97.16' }} xmlSpace="preserve">
                                        <path d="M48.58,0C21.793,0,0,21.793,0,48.58s21.793,48.58,48.58,48.58s48.58-21.793,48.58-48.58S75.367,0,48.58,0z M48.58,86.823    c-21.087,0-38.244-17.155-38.244-38.243S27.493,10.337,48.58,10.337S86.824,27.492,86.824,48.58S69.667,86.823,48.58,86.823z" />
                                        <path d="M73.898,47.08H52.066V20.83c0-2.209-1.791-4-4-4c-2.209,0-4,1.791-4,4v30.25c0,2.209,1.791,4,4,4h25.832    c2.209,0,4-1.791,4-4S76.107,47.08,73.898,47.08z" />
                                    </svg>
                                    <p className="leading-none my-auto">{moment(item.createdAt).format("YYYY-MM-DD")}</p>
                                </div>
                            </div>
                        </Link>
                    ))
                }
            </div>
            {loading && <Loading />}
        </>
    )
}
